.project-card {
  width: 100%;
  background-color: rgb(146, 68, 247);
  display: flex;
  box-shadow: 10px 7px 7.5px rgba(0, 0, 0, 0.295);
}

.project-card-left,
.project-card-right {
  width: 50%;
}

.project-right-container {
  font-size: 2rem;
  flex-basis: auto;
  display: flex;
  justify-content: space-evenly;
  flex-direction: column;
  align-items: center;
  width: 100%;
  height: 100%;
}

.project-card img {
  width: 100%;
  height: 100%;
}

.project-card-title {
  text-align: center;
  margin-bottom: 1%;
  text-decoration: underline;
  text-decoration-color: white;
}

.project-card-subtitle {
  font-style: italic;
  margin: 0;
  text-align: center;
}

.project-card-link-list {
  display: flex;
  width: 50%;
  justify-content: center;
  padding-bottom: 5%;
}

.project-button-link {
  text-decoration: none;
  margin-right: 1%;
  width: 60%;
  text-align: center;
  padding: 2px 5px 2px 5px;
  border-radius: 5%;
  color: white;
  transition: 0.4s ease-in-out;
}

.project-button-link:hover {
  transform: scale(1.2);
  color: black;
  background-color: white;
}

@media only screen and (min-width: 1500px) {
  .project-card-title {
    font-size: 4rem;
  }
}

@media only screen and (max-width: 600px) {
  .project-right-container {
    font-size: 1.5rem;
  }
}

@media only screen and (max-width: 375px) {
  .project-right-container {
    font-size: 1.2rem;
  }
}
